import Vue from "vue";
import axios from "axios";
import store from './store'

Vue.prototype.$debug_host = 'd.18rbbs.com'
Vue.prototype.create_axios = function () {

  var create_object = {
    timeout: 1000 * 100,
  };

  var host = window.location.host;
  this.$web_conf.debug = false;
  if (host.indexOf('localhost') >= 0) {
    create_object['baseURL'] = 'https://' + this.$debug_host;
    this.$web_conf.debug = true;
  }

  console.log('[create_axios]create_object:', create_object);
  Vue.prototype.$axios = axios.create(create_object);

  Vue.prototype.$axios.interceptors.request.use(function (config) {
    config.headers.Authorization = store.state.user.token
    console.log("requests.interceptors.request.use:", config);
    return config;
  }, function (error) {
    return Promise.reject(error)
  });


  Vue.prototype.$axios.interceptors.response.use(
        response => {
            console.log("requests.interceptors.response.use response:", response);
            const res = response.data;
            if (res.code !== 200) {
                if (res.description && res.description.length > 0) {
                    return this.$message(res.description);
                }
                if (res.msg && res.msg.length > 0) {
                    return this.$message(res.msg);
                }
                return Promise.reject(response);
            } else {
                if (res.msg && res.msg.length > 0) {
                    this.$message(res.msg);
                }
                if (res.description && res.description.length > 0) {
                    this.$message(res.description);
                }
                return res;
            }
        }, error => {
            // errorstatus
            let text = "";
            let err = JSON.parse(JSON.stringify(error));
            console.log("xx.requests.interceptors.response.use err:", err);
            if (err.response && err.response.status) {
                console.log("error.response.status:", error.response.status);
                switch (error.response.status) {
                    case 400:
                        text = "(400)";
                        break;
                    case 401:
                        text = "(401)";
                        return this.$router.replace("/login");
                    case 403:
                        text = "(403)";
                        break;
                    case 404:
                        text = "(404)";
                        break;
                    case 408:
                        text = "(408)";
                        break;
                    case 500:
                        text = "(500)";
                        break;
                    case 501:
                        text = "(501)";
                        break;
                    case 502:
                        text = "(502)";
                        break;
                    case 503:
                        text = "(503)";
                        break;
                    case 504:
                        text = "(504)";
                        break;
                    case 505:
                        text = "HTTP(505)";
                        break;
                    default:
                        text = "";
                }
            } else {
                text = ",!";
            }
            return Promise.reject(error);
        }
    );
}

